import React from 'react';
import './CartWidget.css';
import { Link } from 'react-router-dom';
import { Badge } from 'react-bootstrap';
import { useContext } from "react";
import { useCartContext } from "../../../context/CartContext";

function WidgetCart() {
    const { cartList } = useCartContext()
    return (

        <Link className="cart col-3 mx-3 mx-md-1" to="/react/cart">
            <div>
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="white" className="bi bi-bag" viewBox="0 0 16 16">
                    <path d="M8 1a2.5 2.5 0 0 1 2.5 2.5V4h-5v-.5A2.5 2.5 0 0 1 8 1zm3.5 3v-.5a3.5 3.5 0 1 0-7 0V4H1v10a2 2 0 0 0 2 2h10a2 2 0 0 0 2-2V4h-3.5zM2 5h12v9a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V5z"/>
                </svg>
            </div>
            {cartList.length>0 && <Badge className='p-1 cart-counter'>{cartList.length}</Badge>}
        </Link>
    )
}

export default WidgetCart
